___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    user-select: none;
}

html {
    font-size: 10px;
}

@media only screen and (min-width: 35.5em) {
    html {
        font-size: 12px;
    }
}

@media only screen and (min-width: 48em) {
    html {
        font-size: 14px;
    }
}

@media only screen and (min-width: 64em) {
    html {
        font-size: 16px;
    }
}

@media only screen and (min-width: 80em) {
    html {
        font-size: 18px;
    }
}

@media only screen and (min-width: 120em) {
    html {
        font-size: 20px;
    }
}

@media only screen and (min-width: 160em) {
    html {
        font-size: 22px;
    }
}

@media only screen and (min-width: 240em) {
    html {
        font-size: 24px;
    }
}

.dropdown-menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    font-size: 1.2rem;
}

.dropdown-menu-title,
.dropdown-menu[data-hidden="false"] .dropdown-menu-list,
.dropdown-menu[data-hidden="false"] a {
    cursor: pointer;
}

.dropdown-menu-list {
    list-style-type: none;
    opacity: 1;
    transform: translateX(0%) translateY(0.5rem);
    transition: all 1s ease;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.dropdown-menu-title, 
.dropdown-menu-list li {
    background-color: green;
    width: 5rem;
    height: 3.5rem;
    display: flex;
    align-items: center;
    padding-left: 1rem;
    border-radius: 50%;
    color: whitesmoke;
}

.dropdown-menu-title:hover,
.dropdown-menu-list li:hover {
    filter: brightness(0.9);
}

.dropdown-menu-title:active,
.dropdown-menu-list li:active {
    filter: brightness(0.7);
}

.dropdown-menu-list li {
    margin-bottom: 0.5rem;
}

.hide {
    transform: translateX(0%) translateY(0);
    opacity: 0;
}

a {
    color: inherit;
    text-decoration: inherit;
    cursor: default;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,iBAAiB;AACrB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,aAAa;IACb,iBAAiB;AACrB;;AAEA;;;IAGI,eAAe;AACnB;;AAEA;IACI,qBAAqB;IACrB,UAAU;IACV,4CAA4C;IAC5C,uBAAuB;IACvB,gBAAgB;IAChB,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;;IAEI,uBAAuB;IACvB,WAAW;IACX,cAAc;IACd,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;;IAEI,uBAAuB;AAC3B;;AAEA;;IAEI,uBAAuB;AAC3B;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,uCAAuC;IACvC,UAAU;AACd;;AAEA;IACI,cAAc;IACd,wBAAwB;IACxB,eAAe;AACnB","sourcesContent":["* {\r\n    margin: 0;\r\n    padding: 0;\r\n    user-select: none;\r\n}\r\n\r\nhtml {\r\n    font-size: 10px;\r\n}\r\n\r\n@media only screen and (min-width: 35.5em) {\r\n    html {\r\n        font-size: 12px;\r\n    }\r\n}\r\n\r\n@media only screen and (min-width: 48em) {\r\n    html {\r\n        font-size: 14px;\r\n    }\r\n}\r\n\r\n@media only screen and (min-width: 64em) {\r\n    html {\r\n        font-size: 16px;\r\n    }\r\n}\r\n\r\n@media only screen and (min-width: 80em) {\r\n    html {\r\n        font-size: 18px;\r\n    }\r\n}\r\n\r\n@media only screen and (min-width: 120em) {\r\n    html {\r\n        font-size: 20px;\r\n    }\r\n}\r\n\r\n@media only screen and (min-width: 160em) {\r\n    html {\r\n        font-size: 22px;\r\n    }\r\n}\r\n\r\n@media only screen and (min-width: 240em) {\r\n    html {\r\n        font-size: 24px;\r\n    }\r\n}\r\n\r\n.dropdown-menu {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    justify-content: center;\r\n    padding: 1rem;\r\n    font-size: 1.2rem;\r\n}\r\n\r\n.dropdown-menu-title,\r\n.dropdown-menu[data-hidden=\"false\"] .dropdown-menu-list